import { Component, OnInit, ViewChild } from '@angular/core';
import { FileService } from '../services/fileService';
import { Observable } from 'rxjs';
import { AngularFireDatabase } from '@angular/fire/database';
import { BaseComponent } from '../shared/base-component/base-component';

@Component({
  selector: 'app-projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.css']
})
export class ProjectsComponent extends BaseComponent implements OnInit {
  @ViewChild('container') container;
  @ViewChild('header') header;
  @ViewChild('options') options;
  activeOption: string = "construction";
  items: any[] = [];
  imgFiles: Observable<any>;
  _imageSubscription;


  constructor(private fileService: FileService, private db: AngularFireDatabase) {
    super();
  }

  async ngOnInit() {
    this.fileService.getImageDetailList();
    this.imgFiles = this.fileService.getConstructionImages();
    this._imageSubscription = this.imgFiles.subscribe(item => {
      this.items = item;
    });
  }

  getConstructions() {
    if (this.activeOption != 'construction') {
      this._imageSubscription.unsubscribe();
      this.items = [];
      this.activeOption = 'construction';
      this.imgFiles = this.fileService.getConstructionImages();

      this._imageSubscription = this.imgFiles.subscribe(item => {
        this.items = item
      });
    }
  }

  getRepairs() {
    if (this.activeOption != 'repair') {
      this._imageSubscription.unsubscribe();
      this.items = [];
      this.activeOption = 'repair';
      this.imgFiles = this.fileService.getRepairImages();

      this._imageSubscription = this.imgFiles.subscribe(item => {
        this.items = item
      });
    }
  }

  ngOnDestroy() {
    this._imageSubscription.unsubscribe();
  }
}
