import { Injectable, Inject } from '@angular/core';
import { AngularFireList, AngularFireDatabase } from '@angular/fire/database';

@Injectable({
    providedIn: 'root'
})

export class FileService {
    constructionsImg: AngularFireList<any>;
    repairImg: AngularFireList<any>;
    fileList: any[];
    dataSet: Data = {
        id: '',
        type: '',
        url: ''
    };
    msg: string = 'error';

    constructor(private firebase: AngularFireDatabase) { }

    getImageDetailList() {
        this.constructionsImg = this.firebase.list('constructions');
        this.repairImg = this.firebase.list('repair');
    }

    insertImageDetails(id, type, url) {
        this.dataSet = {
            id: id,
            type: type,
            url: url
        };

        if (type == "construction") {
            this.constructionsImg.push(this.dataSet);
        } else if (type == "repair") {
            this.constructionsImg.push(this.dataSet);
        } else {
            console.log("Error!")
        }

    }

    getImage(id) {
        this.constructionsImg.snapshotChanges().subscribe(
            list => {
                this.fileList = list.map(item => { return item.payload.val(); });
                this.fileList.forEach(element => {
                    if (element.id === id)
                        this.msg = element.url;
                });
                if (this.msg === 'error')
                    alert('No record found');
                else {
                    window.open(this.msg);
                    this.msg = 'error';
                }
            }
        );
    }

    getConstructionImages(){
        return this.constructionsImg.valueChanges();
    }

    getRepairImages(){
        return this.repairImg.valueChanges();
    }
}

export interface Data {
    id: string;
    type: string;
    url: string;
}