import { Component, HostListener, OnInit } from '@angular/core';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BaseComponent } from './shared/base-component/base-component';
import { Languages } from 'src/assets/enum/Languages';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  animations: [
    trigger('openClose', [
      state('open', style({
        left: '0%'
      })),
      state('closed', style({
        left: '100%'
      })),
      transition('open => closed', [
        animate('0.5s')
      ]),
      transition('closed => open', [
        animate('0.5s')
      ]),
    ]),
  ]
})
export class AppComponent extends BaseComponent implements OnInit {
  title = 'vipa';
  currentLang: Languages;
  availableLang: Languages = Languages.EN;
  isOpen: boolean = true;
  innerWidth: number;

  constructor(private router: Router, public translate: TranslateService) {
    super();
  }

  ngOnInit() {
    this.innerWidth = window.innerWidth;
    this.isOpen = (this.innerWidth > 700);
    this.translationsInit();
    console.log(this.translate)
  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.innerWidth = window.innerWidth;
    if (this.innerWidth > 700) {
      this.isOpen = true;
    } else {
      this.isOpen = false;
    }
  }

  routeChange() {
    if (this.innerWidth <= 700 && this.isOpen) {
      this.toggleBurger();
    }
  }

  toggleBurger() {
    this.isOpen = !this.isOpen;
  }

  private async translationsInit() {
    await this.translate.use(Languages.EN).toPromise();
    this.currentLang = <Languages>this.translate.currentLang
  }

  async changeLang() {
    await this.translate.use(this.translate.currentLang === Languages.BG ? Languages.EN : Languages.BG ).toPromise();
    this.currentLang = Languages[this.translate.currentLang.toUpperCase()];
  }
}
